<template>
  <div class="webssh">
    <div id="header">
      <div id="terminal"></div>
    </div>
  </div>
</template>
<script>
// vue
// terminalsocket
import gql from "graphql-tag";
import "xterm/css/xterm.css";
import { Terminal } from "xterm";
import { FitAddon } from "xterm-addon-fit";
import { attach } from "xterm-addon-attach";
import { search } from "xterm-addon-search";
const terminal = new Terminal();
import io from "socket.io-client";
let socket;
const fitAddon = new FitAddon();
terminal.loadAddon(fitAddon);

export default {
  data() {
    return {};
  },
  beforeCreate() {
    //
  },
  created() {
    //
  },
  destroyed() {
    //
  },
  mounted() {
    const ele = document.getElementById("terminal");
    terminal.open(ele);
    terminal.focus();
    fitAddon.fit();
    // socket
    // socket
    if (this.$store.state.isLogined == true) {
      socket = io("http://localhost");
      socket.on("connect", function() {
        console.log("client connect");
      });
      socket.on("event", function() {
        console.log("event");
      });
      socket.on("commandRes", commandRes => {
        terminal.write(commandRes);
      });
      socket.on("disconnect", function() {
        console.log("client disconnect");
      });
    } else {
      // this webssh
      // 
      console.log(this.$parent.$message);
      
      this.$parent.$message.error("");
      this.$parent.$parent.$router.push("/login");
    }
    let printable = false;
    // 
    let command = "";
    // terminal.prompt = () => {
    //   terminal.write('\n\r# ')
    // }
    // term
    terminal.onKey(data => {
      const key = data.domEvent.key;
      // 
      printable =
        !data.domEvent.altKey &&
        !data.domEvent.ctrlKey &&
        !data.domEvent.metaKey;

      switch (key) {
        case "Enter":
          // terminalcommand
          for (let i = command.length; i > 0; i--) {
            terminal.write("\b \b");
          }
          this.uploadCommand(command);
          command = "";
          //   terminal.prompt();
          break;
        case "Backspace":
          {
            // 
            command = command.slice(0, -1);
            terminal.write("\b \b");
          }
          break;
        default:
          if (printable) {
            terminal.x++;
            terminal.write(key);
            command += key;
          }
      }
    });
  },
  methods: {
    uploadCommand: function(command) {
      // 
      socket.emit("uploadCommand", command);
    }
  },
  beforeDestroy() {
    // ws
    // this.socket.disconnect()
  }
};
</script>